.feedback-form {
  max-width: 480px;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, .12);
  border-radius: 4px;
  flex-direction: column;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  width: 100%;
  color: #14151a;
  flex-direction: column;
  margin-bottom: 24px;
  font-weight: bold;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  color: #101011;
  border-radius: 10px;
  padding: 8px 12px;
  font-weight: bold;
  transition: all .2s ease-in-out;
}

button:hover {
  box-shadow: -2px -2px 5px #fff, 2px 2px 5px #babecc;
}

button:active {
  box-shadow: inset 1px 1px 2px #babecc, inset -1px -1px 2px #fff;
}

input, textarea {
  color: #101011;
  width: 100%;
  box-sizing: border-box;
  border-radius: 10px;
  padding: 8px;
  transition: all .2s ease-in-out;
}

input:focus, textarea:focus {
  box-shadow: inset 1px 1px 2px #babecc, inset -1px -1px 2px #fff;
}

textarea {
  resize: none;
}

button, input, textarea {
  background-color: #ebecf0;
  border: 0;
  outline: 0;
  padding: 16px;
  font-size: 16px;
}

